/* Contenedor principal de la secci√≥n que contiene las cards*/
.contenedorHoteles {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
}
/* =========================================================== */
/* Contenedor de las cards */
.contenedor-HotelInfo {
  display: flex;
  flex-direction: column;
  width: 350px;
  height: 820px;
  margin: 80px 10px;
  border: 1px rgb(177, 177, 177) solid;
  border-radius: 5px;
  background-color: white;
}
/* Efecto sombra */
.contenedor-HotelInfo:hover {
  -webkit-animation: shadow-drop-2-center 0.4s
    cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
  animation: shadow-drop-2-center 0.4s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
}
@-webkit-keyframes shadow-drop-2-center {
  0% {
    -webkit-transform: translateZ(0);
    transform: translateZ(0);
    -webkit-box-shadow: 0 0 0 0 rgba(0, 0, 0, 0);
    box-shadow: 0 0 0 0 rgba(0, 0, 0, 0);
  }
  100% {
    -webkit-transform: translateZ(50px);
    transform: translateZ(50px);
    -webkit-box-shadow: 0 0 20px 0px rgba(0, 0, 0, 0.35);
    box-shadow: 0 0 20px 0px rgba(0, 0, 0, 0.35);
  }
}
@keyframes shadow-drop-2-center {
  0% {
    -webkit-transform: translateZ(0);
    transform: translateZ(0);
    -webkit-box-shadow: 0 0 0 0 rgba(0, 0, 0, 0);
    box-shadow: 0 0 0 0 rgba(0, 0, 0, 0);
  }
  100% {
    -webkit-transform: translateZ(50px);
    transform: translateZ(50px);
    -webkit-box-shadow: 0 0 20px 0px rgba(0, 0, 0, 0.35);
    box-shadow: 0 0 20px 0px rgba(0, 0, 0, 0.35);
  }
}
/* =========================================================== */
/* Imagenes */
.img {
  height: 100px;
}
/* =========================================================== */
/* Div contenedor de la info (titulo,descripcion,etc.)*/
.info-general {
  display: flex;
  flex-direction: column;
  margin: 10px 20px;
}
/* =========================================================== */
/* Nombre del hotel */
.name {
  margin: 17px 0px;
  font-weight: bold;
  font-size: 22px;
}
/* =========================================================== */
/* Fechas desde y hasta disponible */
.info-fechas {
  display: flex;
  align-items: center;
  height: 40px;
  margin: 5px 0px;
  padding-left: 10px;
  background-color: rgb(131, 126, 120);
  border-radius: 3px;
  color: white;
  font-size: 14px;
}
/* =========================================================== */
/* Descripcion del hotel */
.info {
  font-size: 15px;
  margin: 20px 0px 0px 0px;
}
/* =========================================================== */
/* Contenedores de informacion de lugar, habitaciones y precios */
.rectangulos {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  /* margin-top: auto; */
}
/* =========================================================== */
/* Cada rectangulo con la info */
.country,
.habitaciones,
.precio {
  display: flex;
  flex-wrap: wrap;
  margin-top: 20px;
}
/* =========================================================== */
/* SVGs */
.svg {
  width: 20px;
  height: 20px;
  padding: 16px 20px;
  margin-left: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgb(245, 167, 94);
  color: white;
  border-top-left-radius: 3px;
  border-bottom-left-radius: 3px;
}
/* =========================================================== */
/* texto de va dentro de los divs con info */
.texto-rectangulo {
  display: flex;
  width: auto;
  align-items: center;
  justify-content: center;
  height: 32px;
  margin: 0px;
  padding: 0px 10px;
  font-size: 14px;
  border-top-right-radius: 3px;
  border-bottom-right-radius: 3px;
  background-color: rgb(241, 239, 239);
  color: rgb(63, 63, 63);
}
/* =========================================================== */
/* contenedor SVGs de precios */
.precio .svg {
  border-radius: 3px;
  width: auto;
}
/* =========================================================== */
/* SVG de precios */
.precio .svg svg {
  margin-right: 3px;
}
/* =========================================================== */
/* Boton de reserva */
.boton {
  margin-top: auto;
}
.boton-reserva {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 3.5rem;
  text-align: center;
  color: white;
  border-top: 1px rgb(177, 177, 177) solid;
  background-color: rgb(131, 126, 120);
  border-radius: 5px;
  font-size: 19px;
  cursor: pointer;
}

.boton-reserva:hover {
  background-color: rgb(245, 167, 94);
  font-size: 20px;
}
/* =========================================================== */
/*Mensaje de error */
.mensajeError {
  width: 90%;
  height: 130px;
  background-color: rgb(214, 81, 19);
  color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 26px;
}
/* =========================================================== */
/* RESPONSIVE MOBILE */
@media screen and (max-width: 600px) {
  .mensajeError {
    padding: 20px;
  }
}
